import React from "react";
import { images } from "../img/images";
function Contact() {
  return (
    <main className="contact">
      <div className="container">
        <section className="socials">
          <h2 className="socials__title heading-2">Contact us</h2>
          <p className="socials__text">
            Lorem ipsum, dolor sit amet consectetur adipisicing elit. Illum nisi
            expedita quae facere corporis pariatur?
          </p>
          <ul className="social-list">
            <li className="social__item">
              <a href="/">
                <img src={images.twitter_contact} alt="scoial icon" />
              </a>
            </li>
            <li className="social__item">
              <a href="/">
                <img src={images.facebook_contact} alt="scoial icon" />
              </a>
            </li>
            <li className="social__item">
              <a href="/">
                <img src={images.linkedin_contact} alt="scoial icon" />
              </a>
            </li>
          </ul>
        </section>
        <div className="map">
          <img src={images.map_contact} alt="map" />
        </div>
        <div className="contacts">
          <div className="contacts__inner">
            <form className="form" action="/">
              <h2 className="form__title">Contact Us</h2>
              <input type="text" placeholder="Your Name" />
              <input type="email" placeholder="Your Email" />
              <textarea placeholder="Your Message"></textarea>
              <button className="btn-primary form__btn" type="submit">
                send
              </button>
            </form>
            <ul className="contact-list">
              <li className="contact-list-item">
                <img src={images.mobile_contact} alt="icon" />
                <p>(012) 345 67 89</p>
              </li>
              <li className="contact-list-item">
                <img src={images.mail_contact} alt="icon" />
                <p>loremipsum@lorem.com</p>
              </li>
              <li className="contact-list-item">
                <img src={images.location_contact} alt="icon" />
                <p>
                  6386 Spring St undefined
                  <br />
                  Anchorage,Georgia
                  <br />
                  12473
                  <br />
                  United States
                </p>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </main>
  );
}
export default Contact;
